import { NAMES, DESCRIPTION, objectsNumber } from "./data.js";
import { getRandomArrayElement, createMessage, getRandomIntInclusive,
  checkStringLength
 } from './util.js'



//создаем функцию на создание объекта комментарии
const comment = (index) => {
  return{
    id: index,
    avatar: `img/avatar-${getRandomIntInclusive(1, 6)}.svg`,
    message: createMessage(),
    name: getRandomArrayElement(NAMES),
  }
}

//создаем функцию на создание обекта информации по фото
const infoFoto = (index) => {
  return{
    id: index,
    url: `photos/${index}.ipg`,
    descriptions: getRandomArrayElement(DESCRIPTION),
    likes: getRandomArrayElement(15, 200),
    comments: Array.from(
      {length: getRandomArrayElement(0, 6)},
      (_, commentIndex) => comment(commentIndex + 1)
      ),
  }
};


const infoFotos = () =>
  Array.from(
    {length: objectsNumber},
    (_, fotoIndex) => infoFoto(fotoIndex + 1)
    );

    checkStringLength('', 140);

    export {infoFotos};
